.InTheNewsItem {
    background: #fff 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
    flex: 0 0 calc(50% - 80px);
    height: auto;
    padding: 24px;
    display: flex;
    flex-direction: column;
    gap: 28px;
}

.InTheNewsItemImage {
    margin: 0 auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.InTheNewsItemImage img {
    width: 200px;
    height: 60px;
    margin: 0 auto;
    object-fit: contain;
}

.InTheNewsItem h5 {
    letter-spacing: 0px;
    color: #384F5C;
}

.h4 {
    padding-bottom: 8px;
    text-align: center;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
}

.InTheNewsItemText {
    text-align: center;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    overflow: hidden;
}

@media (max-width: 600px) {
    .InTheNewsItem {
        flex: 0 0 100%;
    }

}

@media (min-width: 601px) and (max-width: 655px) {
    .InTheNewsItem {
        flex: 0 0 calc(50% - 80px);
    }
}

