.DrawerToggle {
  width: 20px;
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  align-items: center;
  padding: 10px 0;
  box-sizing: border-box;
  cursor: pointer;
}

.DrawerToggle div {
  width: 90%;
  height: 2px;
  background-color: #000;
  margin: 2px;
}

@media (min-width: 900px) {
  .DrawerToggle {
      display: none;
  }
}
