.InTheNews {
    width: 100%;
    padding: 5% 8%;
    display: flex;
    justify-content: center;
    align-items: stretch;
    column-gap: 40px;
    row-gap: 40px;
    flex-wrap: wrap;
    overflow-x: hidden;
}

.InTheNewsHide {
    display: none;
}

@media (max-width: 600px) {
    .InTheNews {
        row-gap: 20px;
    }
}

@media (min-width: 601px) and (max-width: 655px) {
    .InTheNews {
        column-gap: 40px;
        padding: 5% 4%;
    }
}

