.PressItem {
    padding: 32px;
    background: #fff 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
    text-align: start;
    position: relative;
    overflow: hidden;
    max-height: 500px;
    transition: max-height 1s cubic-bezier(0, 1, 0, 1);
}

.Expanded {
    max-height: 3000px;
    transition: max-height 1s ease-in-out;
}

.PressItem img {
    float: left;
    width: 320px;
    height: 242px;
    border-radius: 5px;
    margin-right: 32px;
    margin-bottom: 32px;
}

.h3 {
    color: #384F5C;
    padding-bottom: 8px;
    text-align: center;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
}

.PressItem p:first-of-type {
    padding-bottom: 29px;
    text-align: left;
}

.Text {
    /* text-align: center; */
    font-weight:300;
    font-size: 16px;
    line-height: 26px;
    color: #707070;
}

.TextLess {
    overflow: hidden;
}

.TextMore {
    font-size: 15px;
    color: #707070;
    padding-bottom: 32px;
    overflow: hidden;
    visibility: hidden;
    height: 0;
    max-height: 0;
    transition: max-height 1s ease-in-out;
}

.Visible {
    visibility: visible;
    height: auto;
    max-height: 3000px;
    transition: max-height 1s ease-in-out;
}

.Button {
    color: #2189d1;
    border: none;
    background: transparent;
    position: absolute;
    bottom: 14px;
    right: 34px;
    text-align: center;
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
}

@media (max-width: 900px) {
    .PressItem img {
        width: 40%;
        height: auto;
        padding: 0;
        margin: 0;
        margin-right: 10px;
    }
}
