* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.Footer {
    padding: 55px 45px 27px;
    background-color: #f2f2f2;
}

.LogoFooter {
    background-image: url(../../assets/images/logo-transparent.webp);
    display: inline-block;
    width: 15em;
    height: 5.7em;
    background-repeat: no-repeat;
    background-size: 14em 4em;
}

.WorldImage {
    background-image: url(../../assets/images/world_image.png);
    display: inline-block;
    background-repeat: no-repeat;
    position: relative;
    text-align: center;
    padding: 15px;
    width: 7.1em;
    height: 4em;
    background-size: 7em 4em;
}

.FooterBox {
    display: flex;
    justify-content: center;
    padding-bottom:40px;
}

.Footer-Box-Right {
    padding: 0 40px;
    width: 30%;
}

.FooterLeft {
    flex: 0 0 32%;
}

.FooterRight {
    display: flex;
    flex: 0 0 42%;
    color: #414141;
    justify-content: center;
}

.Footer-Box-Right span {
    text-align: center;
    text-transform: uppercase;
    border-bottom: 2px solid #2189d1;
    font-size: 18px;
    letter-spacing: 1px; 
}

.Copyright {
    border-top: 2px solid #70707070;
    padding-top: 38px;
}
.Copyright p {
    font-size: 12px;
    text-align: center;
}

@media (max-width: 720px) {
    .Footer {
        padding: 30px 25px;
    }

    .Footer-Box-Right {
        padding: 20px 10px;
        width: 100%;
    }
}

@media (max-width: 900px) {
    .FooterBox {
        flex-direction: column;
    }

    .FooterLeft {
        text-align: center;
    }


} 
@media (max-width: 450px ) {
    .FooterRight {
        display: block;
    }

}