.InTheNewsTitle {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}

.Hide {
  display: none;
}

.h1 {
  color: #707070;
  text-align: center;
  font-weight: 600;
  font-size: 45px;
  padding-top: 32px;
  padding-bottom: 44px;
  padding-left: 4%;
  padding-right: 4%;
}

.h1 span {
  color: #2189d1;
}

@media (max-width: 1200px) and (min-width: 600px) {
  .h1 {
    font-size: 42px;
    letter-spacing: 0.34px;
  }
}

@media (max-width: 599px) and (min-width: 400px) {
  .h1 {
    font-size: 30px;
    letter-spacing: 0.3px;
  }
}

@media (max-width: 399px) {
    .h1 {
      font-size: 24px;
      letter-spacing: 0.22px;
    }
  }

