.Modal {
    position: fixed;
    background-color: #fff;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px #000;
    padding: 16px;
    left: 15%;
    top: 20%;
    box-sizing: border-box;
    transition: all 0.3 ease-out;
    text-align: center;
}

@media (max-width: 600px) {
    .Modal {
        left: 7.5%;
        top: 10%;
        width: 85%;
    }
}

@media (max-width: 500px)  {
    .Modal {
        overflow-y: auto;
        bottom: 10%;
    }
} 

@media (max-height: 500px) {
    .Modal {
        overflow-y: auto;
        bottom: 10%;
    }

}
@media (min-width: 600) and (min-height: 620px) {
    .Modal {
        bottom: unset;
    }
}

@media (max-height: 800px) and (max-width: 599px) {
    .Modal {
        overflow-y: auto;
        bottom: 10%;
    }
} 

