.NavigationItems {
    list-style: none;
    width: 100%;
    margin: 0 0 0 38px;
    padding: 0;
}

.Right-items {
    float: right;
}
.NavigationItemRight {
    display: inline-block;
    text-decoration: none;
    padding: 7px 6px;
    box-sizing: border-box;
    margin: 0;

}

.NavigationItemRight a {
    font-size: 16px;
    text-decoration: none;
    cursor: pointer;
    color: #777;
    margin: 0 10px;
}

.ButtonNav {
    background-color: #2189d1;
    color: #fff !important;
    text-align: center;
    border: none;
    border-radius: 5px;
    margin-right: 0 !important;
    padding: 8px 25px 10px;
}

@media (max-width: 900px) {
    .NavigationItems {
        display: flex;
        flex-flow: column;
        margin: 0;
    }
    .Right-items {
        display: flex;
        flex-flow: column;
        float: none;
    }
}

@media (max-width: 1100px) {
    .ButtonNav {
        padding: 8px 10px 10px;
    }
}