.Popup {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    flex-direction: row;
    padding: 16px;
}

.Popup button {
    margin: 0 auto;
    text-transform: uppercase;
    color: #fff;
    padding: 16px;
    border-radius: 10px;
    border: none;
    font-size: 16px;
    cursor: pointer;
}

.PopupCol {
    width: 50%;
}

.Popup img {
    margin: 0 auto;
}

.VioletBtn {
    background-color: #8b53a2;
}

.GreenBtn {
    background-color: #00aaa3;
}

.PopupTitel {
    font-size: 26px;
    font-weight: 600;
}

.PopupText {
    font-size: 18px;
    font-weight: 300;
}

@media (max-width: 600px) {
    .Popup {
        display: block;
    }

    .PopupCol {
        width: 100%;
        padding: 20px 0;
    }

    .PopupTitel {
        font-size: 22px;
    }

    .PopupText {
        font-size: 16px;
    }

}