.Toolbar {
    width: 100%;
    position: fixed;
    display: flex;
    flex-direction: row;
    top: 0;
    left: 0;
    box-sizing: border-box;
    background-color: white;
    padding: 20px 8.5%;
    justify-content: space-between;
    align-items: center;
    z-index: 2;
    border-bottom: 1px solid rgb(238, 238, 238);
}

.Toolbar nav {
    display: flex;
    width: 100%;
}

@media (max-width: 900px){
    .Toolbar nav {
        display: none;
    }
}