.TabButtonsContent {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 27px;
}

.TabButtons {
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    letter-spacing: 0.22px;
    color: #707070;
    padding-bottom: 8px;
    text-align: center;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
}

.TabButtons::after {
    content: ' ';
    margin-top: 5px;
    border-bottom: 4px solid transparent;
    width: 100%;
    display: block;
}

.TabButtonsActive::after {
    border-bottom-color: #2189d1;
}
