.MediaBanner {
    width: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    padding: 40px 130px;
    gap: 80px;
}

.MediaBanner img {
    width: 127px;
    height: 155px;
}

.MediaBannerDiv {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    gap: 42px;
}

.MediaBannerText {
    max-width: 250px;
}

.MediaBannerText h3 {
    color: #384F5C;
    padding-bottom: 15px;
    margin-top: 0;
    margin-bottom: 0;
}

.MediaBannerText p {
    padding: 0;
    margin: 0;
}

.MediaBannerText a {
    color: #2189D1;
    text-decoration: none;
}

.LeftAlign {
    text-align: left;
}

.h2 {
    padding-bottom: 16px;
    font-weight: 500;
    font-size: 32px;
    line-height: 38px;
}

.BannerTextBody {
    font-weight: 300;
    font-size: 14px;
    line-height: 20px;
}


@media (max-width: 375px) {
    .MediaBannerDiv {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 16px;
    }
}

@media ( max-width:600px) {
    .MediaBanner {
        flex-direction: column;
        padding: 32px 5%;
    }

    .MediaBannerDiv {
        gap: 16px;
    }

    .MediaBanner img {
        width: 100px;
        height: 125px;
    }

    .h2 {
        font-size: 22px;
        letter-spacing: 0;
    }

    .BannerTextBody {
        font-size: 16px;
        letter-spacing: 0;
    }
    
}

@media (min-width:601px) and (max-width: 900px) {
    .MediaBanner {
        flex-direction: column;
        padding: 32px 0;
        gap: 50px;
    }
}

@media (min-width: 901px) {
    .h2 {
        font-size: 40px;
        line-height: 46px;
        padding-bottom: 32px;
        text-align: left;
    }

    .BannerTextBody {
        font-size: 16px;
        line-height: 24px;
    }
}
