.NavigationItem {
    display: inline-block;
    text-decoration: none;
    padding: 7px 6px;
    margin: 0;
    box-sizing: border-box;
}

.NavigationItem a {
    font-size: 16px;
    text-decoration: none;
    cursor: pointer;
    margin: 0 10px;
    color: #777;
}

.NavigationItem .dropdown-button {
    display: flex;
    cursor: default;
}
.NavigationItem .dropdown-button::after {
    content: ' ';
}

.NavigationItem .arrow-down {
    margin-left: 12px;
    margin-top: 7px;
    border-width: 0 2px 2px 0 !important;
    border: solid #777;
    display: block;
    padding: 1px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    width: 8px;
    height: 8px;
}